.burguer-menu {
  position: absolute;
  top: 0.5rem;
  right: 1rem;
  margin-top: 1rem;
  width: 25px;
  z-index: 5000;
}

.navbar-burguer {
  height: auto;
}

.logo-burguer img {
  padding: 0.5rem;
  width: 60px;
}

.burguer-open,
.burguer-close {
  position: absolute;
  width: 100%;
  height: 2px;
  background: var(--primary-color);
  transition: all 500ms ease;
  border-radius: 10px;
}

.burguer-open::after,
.burguer-close::after {
  content: '';
  position: absolute;
  margin-top: 30%;
  left: 0;
  width: 60%;
  height: 2px;
  background: var(--primary-color);
  border-radius: 10px;
}

.burguer-open::before,
.burguer-close::before {
  content: '';
  position: absolute;
  margin-top: -30%;
  width: 100%;
  height: 2px;
  background: var(--primary-color);
  border-radius: 10px;
}

/** Burguer Close */
.burguer-close::after {
  transform: rotate(-45deg);
  width: 100%;
  margin-top: 0;
  transition: all 500ms ease-out;
}

.burguer-close::before {
  transform: rotate(45deg);
  width: 100%;
  margin-top: 0;
  transition: all 500ms ease-out;
}

.burguer-close {
  background-color: transparent;
}

/** Burguer Nav */
.burguer-nav {
  text-align: center;
  width: 100%;
  height: 100vh;
  background: var(--orange-color);
  overflow: hidden;
  backdrop-filter: blur(10px) contrast(0.9);
  animation: render 500ms ease;
  /* z-index: 100; */
}

.burguer-items {
  margin-left: 2em;
}

.burguer-item {
  font-size: 1.2em;
  font-weight: bold;
  padding: 0.5rem 0;
  margin: 1rem;
  text-transform: uppercase;
  animation: item 1s ease;
}


/** keyframes */
@keyframes item {
  from {
    margin-bottom: 100%;
  }
}
