.footer {
  width: 100%;
  /* padding: .5rem 0; */
  background: var(--dark-color);
  color: #eee;
}

.footer h2 {
  color: var(--orange-color);
  font-size: 2em;
  text-align: center;
}

.footer-content {
  padding: 1rem 0;
  border-top: 1px solid #333;
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.social-media .vertical {
  -webkit-writing-mode: vertical-lr;
      -ms-writing-mode: tb-lr;
          writing-mode: vertical-lr;
}

.social-media  .center {
  margin: auto;
}

.items-social {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.item-social {
  margin-right: 1em;
}

.link-social {
  display: inline-block;
  width: 40px;
  height: 40px;
  border-radius: 50%;
  background: var(--dark-color);
  padding-top: .6rem;
  text-align: center;
  font-size: 1rem;
  font-weight: 700;
}

.link-social:hover {
  color: var(--gray-color);
  background: var(--secondary-color);
}

.header {
  width: 100%;
  min-height: 100vh;
  background: var(--dark-color);
  position: relative;
  overflow: hidden;
  padding-bottom: 2rem;
}

.navbar {
  position: -webkit-sticky;
  position: sticky;
  top: 0;
  width: 100%;
  height: 3.0rem;
  background: var(--dark-color);
  box-shadow: -1px 1px 10px rgba(0, 0, 0, 0.5);
  z-index: 100;
}

.navbar div {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.logo {
  padding-top: .8rem;
  color: var(--orange-color);
}

.logo img {
  width: 2rem;
}

.items {
  display: flex;
  justify-content: center;
  align-items: flex-start;
  height: inherit;
  padding-top: .5rem;
}

.item {
  margin-left: 1rem;
  height: inherit;
}

.link {
  color: var(--gray-color);
  text-transform: uppercase;
  font-weight: 700;
  font-size: 0.65rem;
  padding: .5rem;
  position: relative;
}

.link:hover::before {
  content: '';
  width: 100%;
  height: 2px;
  border-radius: 10px;
  position: absolute;
  top: 0;
  left: 0;
  background: var(--orange-color);
  -webkit-animation: link 500ms ease;
          animation: link 500ms ease;
}

.link:hover {
  color: var(--orange-color);
}

@-webkit-keyframes link {
  0% {
    width: 0;

  }
  100% {
    width: 100%;

  }
}

@keyframes link {
  0% {
    width: 0;

  }
  100% {
    width: 100%;

  }
}

/*
 * ─── HEADER MAIN ────────────────────────────────────────────────────────────────
 */

.header-content {
  width: 100%;
  height: 100%;
  margin-top: 10rem;
  overflow: hidden;
  position: relative;
  z-index: 10;
}

.header::before {
  content: '';
  background-image: url(/static/media/bg.52be3144.svg);
  background-repeat: no-repeat;
  background-size: contain;
  position: absolute;
  right: 5rem;
  top: 5rem;
  width: 30rem;
  height: 100%;
  opacity: 0.3;
  -webkit-animation: render 1s ease;
          animation: render 1s ease;
  z-index: 2;
}

.title-header {
  color: var(--primary-color);
  font-size: 3em;
  text-transform: uppercase;
  letter-spacing: 3.5px;
}

.social {
  width: 200px;
}

.description-header {
  margin: 1.5em 0;
  font-size: .8rem;
  letter-spacing: 0.1rem;
  color: var(--gray-color);
  border-left: 5px solid var(--orange-color);
  padding-left: 0.5rem;
  -webkit-animation: tin 1000ms infinite;
          animation: tin 1000ms infinite;
}

.text-water {
  position: absolute;
  opacity: 0.02;
  font-size: 8em;
  font-weight: 900;
  letter-spacing: 5px;
  bottom: 0;
  right: 0;
}

@-webkit-keyframes up {
  0% {
    opacity: 0;
  }
  100% {
    opacity: 1;
  }
}

@keyframes up {
  0% {
    opacity: 0;
  }
  100% {
    opacity: 1;
  }
}

/*
 * ─── MEDIA QUERIES ──────────────────────────────────────────────────────────────
 */

@media (max-width: 720px) {
  .header {
    font-size: 12px
  }

  .header-content {
    margin-top: 15rem;
  }

  .header::before {
    left: 0;
  }

  .text-water {
    width: 100%;
    font-size: 4.5em;
  }
}
.burguer-menu {
  position: absolute;
  top: 0.5rem;
  right: 1rem;
  margin-top: 1rem;
  width: 25px;
  z-index: 5000;
}

.navbar-burguer {
  height: auto;
}

.logo-burguer img {
  padding: 0.5rem;
  width: 60px;
}

.burguer-open,
.burguer-close {
  position: absolute;
  width: 100%;
  height: 2px;
  background: var(--primary-color);
  -webkit-transition: all 500ms ease;
  transition: all 500ms ease;
  border-radius: 10px;
}

.burguer-open::after,
.burguer-close::after {
  content: '';
  position: absolute;
  margin-top: 30%;
  left: 0;
  width: 60%;
  height: 2px;
  background: var(--primary-color);
  border-radius: 10px;
}

.burguer-open::before,
.burguer-close::before {
  content: '';
  position: absolute;
  margin-top: -30%;
  width: 100%;
  height: 2px;
  background: var(--primary-color);
  border-radius: 10px;
}

/** Burguer Close */
.burguer-close::after {
  -webkit-transform: rotate(-45deg);
          transform: rotate(-45deg);
  width: 100%;
  margin-top: 0;
  -webkit-transition: all 500ms ease-out;
  transition: all 500ms ease-out;
}

.burguer-close::before {
  -webkit-transform: rotate(45deg);
          transform: rotate(45deg);
  width: 100%;
  margin-top: 0;
  -webkit-transition: all 500ms ease-out;
  transition: all 500ms ease-out;
}

.burguer-close {
  background-color: transparent;
}

/** Burguer Nav */
.burguer-nav {
  text-align: center;
  width: 100%;
  height: 100vh;
  background: var(--orange-color);
  overflow: hidden;
  -webkit-backdrop-filter: blur(10px) contrast(0.9);
          backdrop-filter: blur(10px) contrast(0.9);
  -webkit-animation: render 500ms ease;
          animation: render 500ms ease;
  /* z-index: 100; */
}

.burguer-items {
  margin-left: 2em;
}

.burguer-item {
  font-size: 1.2em;
  font-weight: bold;
  padding: 0.5rem 0;
  margin: 1rem;
  text-transform: uppercase;
  -webkit-animation: item 1s ease;
          animation: item 1s ease;
}


/** keyframes */
@-webkit-keyframes item {
  from {
    margin-bottom: 100%;
  }
}
@keyframes item {
  from {
    margin-bottom: 100%;
  }
}

/*
 * props color {primary, secondary, danger, orange, dark}
 * props type {small, large}
 * props text {contenido del boton}
 *
 */

.btn {
  padding: .7rem 1.2rem;
  border: 2px solid transparent;
  font-size: 1rem;
  font-weight: bold;;
  cursor: pointer;
  display: inline-block;
  text-align: center;
  text-transform: uppercase;
  -webkit-transition: all 500ms ease;
  transition: all 500ms ease;
  margin-right: .5rem;
}


/** Type of button */
.large {
  width: 200px;
}
.small {
  width: 150px;
}
.rounded {
  width: 100px;
  font-size: .6rem;
  border-radius: 20px;
  padding: .2rem .4rem;
}

/** Color Button */
.danger {
  background: var(--danger-color);
  color: var(--gray-color);
}


.danger:hover {
  border-color: var(--danger-color);
  color: var(--danger-color);
  background: transparent;
}

.primary {
  background: var(--primary-color);
  color: var(--dark-color)
}

.primary:hover {
  background: transparent;
  border-color: var(--primary-color);
  color: var(--primary-color)
}

.secondary {
  background: var(--secondary-color);
  color: var(--primary-color);
}

.secondary:hover {
  background: transparent;
  border-color: var(--secondary-color);
  color: var(--secondary-color);
}

.dark {
  background: var(--dark-color);
  color: var(--primary-color);
}

.dark:hover {
  background: transparent;
  border-color: var(--dark-color);
  color: var(--dark-color);
}

.orange {
  background: transparent;
  border-color: var(--orange-color);
  color: var(--orange-color);
}

.orange:hover {
  background: var(--orange-color);
  color: var(--primary-color);
}
.about {
  width: 100%;
  height: 100%;
  padding: 4rem 0 7rem 0;
  text-align: center;
  position: relative;
  background-color: #020303cc;
  /* margin-bottom: -5%; */
}

.about::before {
  content: '';
  width: 100%;
  height: 100%;
  position: fixed;
  top: 5%;
  left: 0;
  background-image: url(/static/media/bg-contact.df403ecb.jpg);
  background-size: cover;
  background-repeat: no-repeat;
  background-position: end;
  z-index: -1;
}

.image-about {
  border-radius: 50%;
  border: 10px solid var(--orange-color);
  margin-bottom: 1rem;
  -webkit-animation: tin 500ms ease;
          animation: tin 500ms ease;
}

.description-about {
  font-size: .9rem;
  font-family: Tahoma;
  letter-spacing: 1.5px;
  padding: 0 1rem;
  line-height: 1.7;
  color: var(--primary-color);
}

.quote {
  color: var(--gray-color);
  font-size: 2em;
  padding: 0 2rem;
}

.description-about span {
  display: block;
  margin: 1rem auto;
  width: 50px;
  height: 5px;
  background: var(--orange-color);
}

.portfolio {
  width: 100%;
  position: relative;
}

.wrapper-portfolio {
  width: 100%;
  background: var(--dark-color);
  display: flex;

}

.arrow-left, .arrow-right {
  position: absolute;
  top: 10%;
  /* transform: translateY(-50%); */
  /* font-size: 1.5rem; */
  z-index: 5;
  padding: 1rem;
  background: var(--secondary-color);
  color: var(--primary-color);
  border-radius: 50%;
  cursor: pointer;
  opacity: .7;
  -webkit-transition: all 500ms;
  transition: all 500ms;
}

.arrow-left {
  left: 1rem;
}

.arrow-right {
  right: 1rem;
}

.arrow-left:hover,
.arrow-right:hover {
  color: var(--orange-color);
  opacity: 1;
}

.wrapper-project {
  width: 100%;
  min-height: 250px;
  height: auto;
  background: var(--dark-color);
  margin: 0 auto;
  overflow: hidden;
  /* box-shadow: -5px 5px 15px 5px rgba(0, 0, 0, 0.637);   */
  position: relative;
}

.info {
  width: 100%;
  height: 100%;
  position: absolute;
  top: 100%;
  left: 0;
  text-align: center;
  padding-top: 5px;
  -webkit-transition: all 500ms ease;
  transition: all 500ms ease;
  /* background: var(--secondary-color); */
}

.wrapper-project:hover .info {
  top: 40%; 
}

.wrapper-project img {
  border: 5px solid var(--dark-color);
  opacity: .7;
  -webkit-transition: all 500ms ease;
  transition: all 500ms ease;
  width: 100%;
  height: 100%;
}

.wrapper-project:hover img {
  -webkit-transform: scale(1.05);
          transform: scale(1.05);
  opacity: 0.2;
}

.title-project {
  color: var(--orange-color);
  /* border-bottom: 5px solid #6e6e6e; */
  font-size: 1rem;
  text-transform: uppercase;
  margin: .5rem 5px;
  padding: .5rem 0;
  background: var(--dark-color);
}

/** Icons */
.wrapper-icon {
  margin: 1rem;
  position: absolute;
  right: 0;
  top: 0;
  font-size: 2rem;
  opacity: .9;
}

.info i {
  /* background: var(--secondary-color); */
  color: var(--primary-color);
  padding: .5rem;
  margin: 1rem;
  border: 2px solid var(--gray-color);
  border-radius: 10px;
  -webkit-transition: -webkit-transform 500ms ease;
  transition: -webkit-transform 500ms ease;
  transition: transform 500ms ease;
  transition: transform 500ms ease, -webkit-transform 500ms ease;
}

.info i:hover {
  -webkit-transform: scale(1.3);
          transform: scale(1.3);
}

.skills {
  height: 100%;
  width: 100%;
  padding: 4rem 0;
  background-color: var(--secondary-color);
}

.wrapper-skills {
  height: 100%;
  display: grid;
  grid-template-columns: 1fr 1fr;
  align-items: center;
  justify-content: center;
  /* grid-gap: 2rem; */
  position: relative;
}

.title-skill {
  margin: .5rem 0;
  font-size: 1rem;
  letter-spacing: 2px;
  text-transform: uppercase;
}

.description-skill {
  color: var(--gray-color);
  text-align: left;
  width: 80%;
  margin: 1rem auto;
  font-size: .8rem;
  line-height: 1.7;
}


/********************** */

.circle, .line {
  position: absolute;
  left: 50%;
  -webkit-transform: translateX(-50%);
          transform: translateX(-50%);
}

.circle {
  width: 30px;
  height: 30px;
  background-color: var(--secondary-color);
  border: 2px solid var(--orange-color);
  border-radius: 25%;
}

.circle:nth-child(2) {
  top: 25%;
}

.circle:nth-child(4) {
  top: 50%;
}

.circle:nth-child(3) {
  top: 75%;
}

.line {
  height: 100%;
  width: .2rem;
  background-color: var(--orange-color);
}

.skill {
  padding: 2rem 4rem;
  text-align: center;
}

/** ************* icons*************  */
.skill i {
  font-size: 2.5rem;
  -webkit-animation: render 500ms ease;
          animation: render 500ms ease;
}

.fa-react {
  color: #5ED4F4;
}

.fa-js {
  color: #F0D91D;
}

.fa-node-js {
  color: #8CC03D;
}

.fa-html5 {
  color: #DE4B25;
}

.fa-css3 {
  color: #0096D1;
}


.button-up {
  background: var(--orange-color);
  cursor: pointer;
  border-radius: 50%;
  position: sticky;
  position: -webkit-sticky;
  bottom: 1rem;
  float: right;
  margin-right: 1rem;
  -webkit-transition: all 500ms ease;
  transition: all 500ms ease;
  text-align: center;
  z-index: 10;
}


.button-up:hover {
  background: var(--gray-color);
}

.fa-angle-double-up {
  color: var(--primary-color);
  font-size: 1.2rem;
  padding: .5rem .7rem;
}

.button-up:hover .fa-angle-double-up {
  color: var(--orange-color);
}
.contact {
  width: 100%;
  /* padding-top: 5rem; */
  background: var(--dark-color);
}

.form-contact {
  /* width: 55%; */
  display: flex;
  justify-content: center;
  align-items: flex-end;
  padding: 1rem;
  margin: auto;
}

.input {
  width: 100%;
  margin: 1rem;
}

input, textarea, button {
  width: 100%;
  font-size: .7em;
  display: block;
  padding: 0.8rem;
  background: var(--secondary-color);
  color: var(--primary-color);
  border: none;
  border-radius: 5px;
  box-shadow: 0 0 7px rgba(0, 0, 0, 0.24);
}

.btn-contact {
  background: var(--orange-color);
  color: var(--primary-color);
  cursor: pointer;
  -webkit-transition: all 400ms ease;
  transition: all 400ms ease;
  font-weight: bold;
  text-transform: uppercase;
}

.btn-contact:hover {
  opacity: .7;
}
