@import url('https://fonts.googleapis.com/css?family=Raleway:400,700,900&display=swap');

:root {
  --primary-color: #ffffff;
  --gray-color: #eeeeee;
  --secondary-color: #1e3044;
  --orange-color: #ff6642;
  --orange-color: #087baa;
  /* --danger-color: #c70039; */
  --dark-color: #1c2329;
  --font-small: 0.8rem;
  --font-big: 1.1rem;
}

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

html {
  scroll-behavior: smooth;
  font-size: 18px;
}

body {
  background: var(--secondary-color);
  color: var(--primary-color);
  font-family: 'Raleway', sans-serif;
}

/** Global Configuration */
.container {
  width: 80%;
  margin: 0 auto;
  overflow: hidden;
}

ul {
  list-style: none;
}

a {
  text-decoration: none;
  color: var(--primary-color);
}

/** MEDIA QUERIES DISPLAY LARGE */
@media (min-width: 1480px) {
  .container {
    width: 80%;
  }

  h1 {
    font-size: 5em !important;
  }
}

/*
 * ─── MEDIA QUERIES MOBILE ───────────────────────────────────────────────────────
 */

@media (max-width: 720px) {
  body {
    font-size: 14px;
  }

  .container {
    width: 90%;
  }

  .wrapper-portfolio {
    flex-direction: column;
  }

  .info {
    position: initial !important;
  }

  .arrow-left,
  .arrow-right {
    display: none;
  }

  .wrapper-skills {
    grid-template-columns: 1fr !important;
  }

  .skill {
    padding: 1.5rem;
    text-align: justify;
  }

  .line,
  .circle {
    left: 5% !important;
  }

  .form-contact {
    flex-direction: column;
    align-items: center !important;
  }
  footer .social-media {
    max-width: 100% !important;
  }

  .footer-content {
    flex-direction: column;
  }
}

/**********************************************************************************
 * ─── KEYFRAMES ──────────────────────────────────────────────────────────────────
 */

@keyframes tin {
  0% {
    opacity: 0;
  }
  100% {
    opacity: 1;
  }
}

@keyframes render {
  from {
    transform: scale(0);
  }
  to {
    transform: scale(1);
  }
}

