.contact {
  width: 100%;
  /* padding-top: 5rem; */
  background: var(--dark-color);
}

.form-contact {
  /* width: 55%; */
  display: flex;
  justify-content: center;
  align-items: flex-end;
  padding: 1rem;
  margin: auto;
}

.input {
  width: 100%;
  margin: 1rem;
}

input, textarea, button {
  width: 100%;
  font-size: .7em;
  display: block;
  padding: 0.8rem;
  background: var(--secondary-color);
  color: var(--primary-color);
  border: none;
  border-radius: 5px;
  box-shadow: 0 0 7px rgba(0, 0, 0, 0.24);
}

.btn-contact {
  background: var(--orange-color);
  color: var(--primary-color);
  cursor: pointer;
  transition: all 400ms ease;
  font-weight: bold;
  text-transform: uppercase;
}

.btn-contact:hover {
  opacity: .7;
}